<template>
  <div style="height:100%;">
    <el-container style="height:100%;">
      <el-header class="header">
        <div class="headword flex-row-between">
          <div>您好 {{userInfo.userName}} 欢迎访问系统应用！</div>
          <logoutbtn></logoutbtn>
        </div>
      </el-header>
      <el-main class="main">
        <el-row :gutter="18">
          <el-col :span="16" :offset="4">
            <el-card class="box-card" style="margin-bottom:10px;">
              <div class="logoBox">
                <img src="../../assets/images/logo/logo2.png" alt="">
              </div>
              <div class="logoContent">
                <div class="flex-row-between">
                  <div class="flex-row-start">
                    <div class="img">
                      <img :src="userInfo.headImageUrl ? userInfo.headImageUrl : require('../../assets/images/selectShop/userDefault.png')" alt="">
                    </div>
                    <div class="info flex-column-start">
                      <div class="flex-row-start">
                        <div class="name">{{userInfo.userName}}</div>
                        <el-button type="primary" plain size="mini" @click="editShowFun">账户设置</el-button>
                      </div>
                      <div class="phone">{{userInfo.cellPhone}}</div>
                    </div>
                  </div>
                  <div class="flex-column-center">
                    <el-button type="primary" size="mini" @click="createShop">创建更多店铺</el-button>
                    <div class="canCreate">可创建店铺：{{userInfo.shopCount}}</div>
                  </div>
                </div>
              </div>
            </el-card>
            <el-card class="box-card">
              <div class="box-head">店铺列表</div>
              <div class="flex-row-start fw">
                <el-card v-for="(item,index) in shopList" :key="index" shadow="hover" class="shopSelect">
                  <div @click="gotoIndex(item)">
                    <div class="posFather">
                      <div class="flex-row-start">
                        <div class="shopImg">
                          <img :src="item.logoUrl ? item.logoUrl : require('../../assets/images/selectShop/defaultShop.png')" alt="">
                        </div>
                        <div class="shopname">{{item.shopName}}</div>
                      </div>
                      <div class="posBox">
                        <div class="flex-row-start" v-if="item.authStatus == 2">
                          <div class="shopVail">
                            <img src="../../assets/images/selectShop/yes.png" alt="">
                          </div>
                          <span class="green font">已认证</span>
                        </div>
                        <div class="flex-row-start" v-else>
                          <div class="shopVail">
                            <img src="../../assets/images/selectShop/no.png" alt="">
                          </div>
                          <span class="red font">未认证</span>
                        </div>
                      </div>
                    </div>
                    <div class="shopStatus">
                      <el-tag type="info" v-if="item.status != 0">{{item.shopType == 2 ? '教育' : '微商城'}}</el-tag>
                      <el-tag v-else-if="item.shopType == 2">教育</el-tag>
                      <el-tag v-else-if="item.shopType == 1" type="danger">微商城</el-tag>
                    </div>
                    <!--  -->
                    <div class="shopStatus">店铺状态：
                      <span v-if="item.status == 0">启用</span>
                      <span style="color:#F56C6C;" v-if="item.status == 1">已过期</span>
                      <span style="color:#F56C6C;" v-if="item.status == 2">已冻结</span>
                    </div>
                    <div class="shopStatus" v-if="item.endTimeType == 0">有效期至： {{item.endTime}}</div>
                    <div class="shopStatus redSpan" style="color:#F56C6C;" v-if="item.endTimeType == 1">有效期至： 已过期</div>
                    <div class="shopStatus redSpan" v-if="item.endTimeType == 2">有效期至： {{item.endTime}}</div>
                    <div class="flex-row-end">
                      <el-button type="text" @click.stop="editShop(item.shopId)">编辑</el-button>
                      <el-button type="text" @click.stop="freezeShop(item.shopId)" v-if="item.endTimeType != 1 && item.status == 0">冻结</el-button>
                      <el-button type="text" @click.stop="openShop(item.shopId)" v-if="item.endTimeType != 1 && item.status == 2">启用</el-button>
                      <el-button type="text" style="color:#999;" @click.stop="deleteShop(item.shopId)" v-if="item.endTimeType == 1">删除</el-button>
                    </div>
                  </div>
                </el-card>
              </div>
            </el-card>
          </el-col>
        </el-row>
      </el-main>
    </el-container>
    <!-- 选择创建类型 -->
    <el-dialog title="请选择您需要创建店铺的类型" :visible.sync="selectShow" width="70%">
      <div class="flex-row-center">
        <div class="selectImg" @click="$router.push({path:'/newShop',query:{shopType:1}})">
          <img src="../../assets/images/selectShop/kzwsc.png" alt="">
        </div>
        <div class="selectImg" @click="$router.push({path:'/newShop',query:{shopType:3}})">
          <img src="../../assets/images/selectShop/kzyd.png" alt="">
        </div>
      </div>
    </el-dialog>
    <!-- 创建店铺  -->
    <el-dialog title="创建店铺" :visible.sync="createShow" width="40%">
      <el-form :model="createForm" ref="createForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="店铺名称：" required>
          <el-input v-model="createForm.shopName" maxlength="20" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="主营商品：" required>
          <el-select v-model="createForm.categoryCode" placeholder="请选择主营产品">
            <el-option v-for="item in industry" :key="item.id" :label="item.name" :value="item.code"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="省市区" required>
          <el-col :span="8">
            <el-form-item required>
              <el-select @change="address1" v-model="createForm.addressProvinceCode" placeholder="请选择省">
                <el-option v-for="(item,index) in addressList1" :key="index" :label="item.name" :value="item.code"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item required>
              <el-select @change="address2" v-model="createForm.addressCityCode" placeholder="请选择市">
                <el-option v-for="(item,index) in addressList2" :key="index" :label="item.name" :value="item.code"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item required>
              <el-select v-model="createForm.addressCountyCode" placeholder="请选择区">
                <el-option v-for="(item,index) in addressList3" :key="index" :label="item.name" :value="item.code"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item label="详细地址：" required>
          <el-input v-model="createForm.addressDetails" maxlength="50" show-word-limit></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="createShow = false">取 消</el-button>
        <el-button type="primary" :loading="createLoading" @click="createShopFun('createForm')">创建店铺</el-button>
      </span>
    </el-dialog>
    <!-- 编辑店铺 -->
    <el-dialog title="编辑店铺" :visible.sync="editShopShow" width="40%">
      <el-form :model="editShopForm" :rules="rules" ref="editShopForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="店铺名称：" required>
          <el-input v-model="editShopForm.shopName" maxlength="20" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="主营商品：" required>
          <el-select v-model="editShopForm.categoryCode" placeholder="请选择主营产品">
            <el-option v-for="item in industry" :key="item.id" :label="item.name" :value="item.code"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="省市区" required>
          <el-col :span="8">
            <el-form-item required>
              <el-select @change="editaddress1" v-model="editShopForm.addressProvinceCode" placeholder="请选择省">
                <el-option v-for="(item,index) in addressList1" :key="index" :label="item.name" :value="item.code"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item required>
              <el-select @change="editaddress2" v-model="editShopForm.addressCityCode" placeholder="请选择市">
                <el-option v-for="(item,index) in addressList2" :key="index" :label="item.name" :value="item.code"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item required>
              <el-select v-model="editShopForm.addressCountyCode" placeholder="请选择区">
                <el-option v-for="(item,index) in addressList3" :key="index" :label="item.name" :value="item.code"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item label="详细地址：" required>
          <el-input v-model="editShopForm.addressDetails" maxlength="50" show-word-limit></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editShopShow = false">取 消</el-button>
        <el-button type="primary" :loading="editLoading" @click="editShopFun('editShopForm')">保 存</el-button>
      </span>
    </el-dialog>
    <!-- 管理员基本信息 ok -->
    <el-dialog title="管理员基本信息" :visible.sync="editShow" width="30%">
      <div>
        <el-form ref="form" :model="editForm" label-width="120px">
          <el-form-item label="真实姓名：">
            <el-input style="width:260px;" v-model="editForm.userName" @input="change($event)"></el-input>
          </el-form-item>
          <el-form-item label="登录账号：">
            <el-input style="width:260px;" disabled v-model="editForm.zh"></el-input>
          </el-form-item>
          <el-form-item label="联系方式：">
            <el-input style="width:260px;" disabled v-model="editForm.zh"></el-input>
          </el-form-item>
          <el-form-item label="常用邮箱：">
            <el-input style="width:260px;" v-model="editForm.email" @input="change($event)"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editShow = false">取 消</el-button>
        <el-button type="primary" plain @click="passwordShow = true">修改密码</el-button>
        <el-button type="primary" :loading="editLoading" @click="editFun">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 修改密码 -->
    <el-dialog title="修改密码" :visible.sync="passwordShow" width="30%">
      <el-form :model="passwordForm" status-icon :rules="rules" ref="passwordForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="原密码：" prop="oldPassword">
          <el-input type="password" v-model="passwordForm.oldPassword"></el-input>
        </el-form-item>
        <el-form-item label="新密码：" prop="newPassword">
          <el-input type="password" auto-complete="new-password" autocomplete="off" v-model="passwordForm.newPassword"></el-input>
        </el-form-item>
        <el-form-item label="确认密码：" prop="checkPass">
          <el-input type="password" auto-complete="new-password" autocomplete="off" v-model="passwordForm.checkPass"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="passwordShow = false">取 消</el-button>
        <el-button type="primary" :loading="changePassLoading" @click="changePass('passwordForm')">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import logoutbtn from "@/components/kzBtns/logoutBtn";
import { 
  getAddressLevel ,
  getShopCategoryList ,
  addShop
} from '@/api/shop/createShop'
import { 
  getShopInfoList ,
  getUserInfo ,
  updateShopStatus,
  updateUserInfo,
  updatePassword,
  getShopInfoById,
  updateShopInfo
} from '@/api/shop/selectShop'
export default {
  data(){
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'));
      } else if (value !== this.passwordForm.newPassword) {
        callback(new Error('两次输入密码不一致!'));
      } else {
        callback();
      }
    };
    return{
      shopList:[],                  // 店铺列表
      industry:[],                  // 创建 - 下拉 - 行业
      addressList1:[],              // 创建 - 下拉 - 地址1级
      addressList2:[],              // 创建 - 下拉 - 地址2级
      addressList3:[],              // 创建 - 下拉 - 地址3级
      createShow:false,             // 创建店铺弹出框
      userInfo:{},                  // 商户信息
      editShow:false,               // 编辑 - 用户信息 - 提示框展示
      editForm:{},                  // 编辑 - 用户信息 - 表格数据
      editLoading:false,            // 编辑 - 用户信息 - 提交loading
      passwordForm:{                // 编辑 - 用户信息 - 修改密码数据
        oldPassword:'',
        newPassword:'',
        checkPass:''
      },
      changePassLoading:false,      // 编辑 - 用户信息 - 修改密码loading
      createForm:{},                // 创建 - 店铺的数据
      createLoading:false,          // 创建 - 按钮loading
      editShopShow:false,           // 编辑店铺
      editShopForm:{},
      passwordShow:false,           // 修改密码弹出框
      rules: {
        oldPassword: [
          { required: true, message: '请输入原始密码', trigger: 'blur' },
          { min: 6, max: 12, message: '长度在 6 到 12 个字符', trigger: 'blur' }
        ],
        newPassword: [
          { required: true, message: '请输入新密码', trigger: 'blur' },
          { min: 6, max: 12, message: '长度在 6 到 12 个字符', trigger: 'blur' }
        ],
        checkPass: [
          { required: true, validator: validatePass, trigger: 'blur' }
        ],
      },
      readonlyInput:true,
      selectShow:false
    }
  },
  created(){
    this.$store.commit('SET_LOAD_MENUS',true)
    this.$store.commit('SET_PERMISSION_STATUS',true)
    this.getList()
    this.getUserInfo()
    this.getShopCategoryList()
    this.getAddressInput(1,'')
  },
  methods:{
    // 测试
    cancelReadOnly() {
      this.readonlyInput= false;
    },
    // 用户信息 - 编辑 - 弹出框
    editShowFun(){
      this.editForm.zh = this.userInfo.cellPhone
      this.editForm.id = this.userInfo.id
      this.editForm.userName = this.userInfo.userName
      this.editForm.email = this.userInfo.email
      this.editShow = true;
    },
    // 用户信息 - 编辑 -提交
    editFun(){
      console.log('编辑用户信息请求的数据',this.editForm)
      this.editLoading = true;
      updateUserInfo(this.editForm).then(res=>{
        console.log('编辑用户信息数据',res)
        if(res.code == 200){
          this.$message({
            message: '修改成功',
            type: 'success'
          });
          this.getUserInfo()
          this.editShow = false;
          setTimeout(()=>{
            this.editLoading = false;
          },1000)
        }else{
          this.$message.error(res.message);
        }
      })
    },
    // 用户信息 - 编辑 - 修改密码
    changePass(formName){
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.changePassLoading = true;
          this.passwordForm.userId = this.user.userId
          updatePassword(this.passwordForm).then(res=>{
            console.log('编辑用户信息数据',res)
            if(res.code == 200){
              this.$message({
                message: '修改成功',
                type: 'success'
              });
              this.passwordShow = false;
              setTimeout(()=>{
                this.changePassLoading = false;
              },1000)
            }else{
              this.$message.error(res.message);
            }
          })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    // 默认 - 获取店铺列表
    getList(){
      getShopInfoList(this.user.userId || localStorage.getItem('userId'),1).then(res=>{
        console.log('获取店铺列表',res)
        this.shopList = res.data
      })
    },
    // 默认 - 获取商户信息
    getUserInfo(){
      console.log('this.user.userId',this.user.userId,localStorage.getItem('userId'))
      getUserInfo(this.user.userId || localStorage.getItem('userId')).then(res=>{
        console.log('获取商户信息',res)
        if(res.code == 200){
          this.userInfo = res.data;
          this.$store.commit('SET_USER_NAME',res.data.userName)
        }
      })
    },
    // 编辑店铺 - 弹出框
    editShop(id){
      getShopInfoById(id).then(res=>{
        console.log('查看店铺详情',res)
        this.editShopForm = res.data;
        this.getAddressInput(2,res.data.addressProvinceCode)
        this.getAddressInput(3,res.data.addressCityCode)
      })
      this.editShopShow = true;
    },
    // 编辑店铺 - 店铺
    editShopFun(formName){
      if(!this.editShopForm.shopName){
        this.$message.error('请输入店铺名称');
        return;
      }
      if(!this.editShopForm.categoryCode){
        this.$message.error('请选择店铺主营产品');
        return;
      }
      if(!this.editShopForm.addressProvinceCode){
        this.$message.error('请选择省');
        return;
      }
      if(!this.editShopForm.addressCityCode){
        this.$message.error('请选择市');
        return;
      }
      if(!this.editShopForm.addressCountyCode){
        this.$message.error('请选择区');
        return;
      }
      if(!this.editShopForm.addressDetails){
        this.$message.error('请填写详细地址');
        return;
      }
      this.editLoading = true;
      console.log('编辑店铺的请求数据',this.editShopForm)
      updateShopInfo(this.editShopForm).then(res=>{
        console.log('编辑店铺',res)
        if(res.code == 200){
          this.$message({
            message: '编辑店铺成功',
            type: 'success'
          });
          this.getList()
          this.editShopShow = false;
          setTimeout(()=>{
            this.editLoading = false;
          },1000)
        }else{
          this.$message.error(res.message);
        }
      })
    },
    // 编辑店铺 - 下拉地址 2级
    editaddress1(val){
      this.addressList2 = [];
      if(this.editShopForm.addressCityCode) this.editShopForm.addressCityCode = '';
      this.addressList3 = [];
      if(this.editShopForm.addressCountyCode) this.editShopForm.addressCountyCode = '';
      this.getAddressInput(2,val);
    },
    // 编辑店铺 - 下拉地址 3级
    editaddress2(val){
      this.addressList3 = [];
      if(this.editShopForm.addressCountyCode) this.editShopForm.addressCountyCode = '';
      this.getAddressInput(3,val);
    },
    // 列表操作 - 开启店铺
    openShop(id){
      this.$confirm('此操作将启用该店铺, 是否继续?', '启用店铺', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let obj = {}
        obj.status = 0;
        obj.shopId = id;
        this.updateShopStatus(obj);
      }).catch(() => {});
    },
    // 列表操作 - 冻结 
    freezeShop(id){
      this.$confirm('此操作将冻结该店铺, 是否继续?', '店铺冻结', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let obj = {}
        obj.status = 2;
        obj.shopId = id;
        this.updateShopStatus(obj);
      }).catch(() => {});
    },
    // 列表操作 - 删除
    deleteShop(id){
      this.$confirm('此操作将永久删除该店铺, 是否继续?', '店铺删除', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let obj = {}
        obj.status = 3;
        obj.shopId = id;
        this.updateShopStatus(obj);
      }).catch(() => {});
    },
    // 列表操作 - 变更店铺状态
    updateShopStatus(obj){
      updateShopStatus(obj).then(res=>{
        console.log('变更状态',res)
        if(res.code == 200){
          this.$message({
            type: 'success',
            message: res.message
          });
          this.getList()
          this.getUserInfo()
        }else{
          this.$message.error(res.message);
        }
      })
    },
    // 选择店铺跳转首页
    gotoIndex(obj){
      this.$store.commit('SET_SHOP_ID',obj.shopId)
      this.$store.commit('SET_SHOP_NAME',obj.shopName)
      this.$store.commit('SET_SHOP_LOGO',obj.logoUrl)
      this.$router.push({
        path:'/shopIndex'
      })
    },
    // 创建 - 创建店铺
    createShopFun(formName){
      if(!this.createForm.shopName){
        this.$message.error('请输入店铺名称');
        return;
      }
      if(!this.createForm.categoryCode){
        this.$message.error('请选择店铺主营产品');
        return;
      }
      if(!this.createForm.addressProvinceCode){
        this.$message.error('请选择省');
        return;
      }
      if(!this.createForm.addressCityCode){
        this.$message.error('请选择市');
        return;
      }
      if(!this.createForm.addressCountyCode){
        this.$message.error('请选择区');
        return;
      }
      if(!this.createForm.addressDetails){
        this.$message.error('请填写详细地址');
        return;
      }
      this.createLoading = true;
      this.createForm.userId = this.user.userId
      console.log('创建店铺的请求数据',this.createForm)
      addShop(this.createForm).then(res=>{
        console.log('创建店铺',res)
        if(res.code == 200){
          this.$message({
            message: '创建店铺成功',
            type: 'success'
          });
          this.getList()
          this.getUserInfo()
          this.createShow = false;
          this.createForm.shopName = '';
          this.createForm.categoryCode = '';
          this.createForm.addressProvinceCode = '';
          this.createForm.addressCityCode = '';
          this.createForm.addressCountyCode = '';
          this.createForm.addressDetails = '';
          setTimeout(()=>{
            this.createLoading = false;
          },1000)
        }else{
          this.$message.error(res.message);
        }
      })
    },
    // 创建 - 店铺
    createShop(){
      if(this.userInfo.shopCount == 0){
        this.$confirm('请联系专属客服：010-86466211', '创建店铺', {
          confirmButtonText: '确定',
          cancelButtonText:'确定',
          showConfirmButton:false,
          type: 'warning'
        }).then(() => {}).catch(() => {});
        return;
      }
      this.selectShow = true;
    },
    selectType(i){
      this.createForm.shopType = i;
      this.selectShow = false;
      this.createShow = true;
    },
    // 创建店铺 - 下拉选择主营
    getShopCategoryList(){
      getShopCategoryList().then(res=>{
        console.log('获取下拉行业',res)
        this.industry = res.data
      })
    },
    // 创建店铺 - 下拉选择地址
    getAddressInput(id1,id2){
      getAddressLevel(id1,id2).then(res=>{
        console.log('获取下拉框地址',res)
        if(id1 == 1){
          this.addressList1 = res.data
          return;
        }
        if(id1 == 2){
          this.addressList2 = res.data
          return;
        }
        if(id1 == 3){
          this.addressList3 = res.data
          return;
        }
      })
    },
    // 创建店铺 - 下拉地址 2级
    address1(val){
      this.addressList2 = [];
      if(this.createForm.addressCityCode) this.createForm.addressCityCode = '';
      this.addressList3 = [];
      if(this.createForm.addressCountyCode) this.createForm.addressCountyCode = '';
      this.getAddressInput(2,val);
    },
    // 创建店铺 - 下拉地址 3级
    address2(val){
      this.addressList3 = [];
      if(this.createForm.addressCountyCode) this.createForm.addressCountyCode = '';
      this.getAddressInput(3,val);
    },
    // input 
    change(e){
      this.$forceUpdate()
    }
  },
  components: {
    logoutbtn
  },
  computed: {
    ...mapState([
      'user'
    ])
  },
}
</script>
