<template>
  <div>
    <el-button class="btn" @click="logout" icon="el-icon-switch-button" type="text">退出系统</el-button>
  </div>
</template>
<script>
export default {
  data(){
    return{}
  },
  methods:{
    logout(){
      console.log('登出了btns')
      this.$confirm('确定要退出系统吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '登出成功!'
        });
        this.$store.dispatch('LogOut').then(() => {
          this.$store.commit('userType', 0) // 0 为正常登录 清楚代管理用户类型
          this.$router.push({path:'/login'}) // 为了重新实例化vue-router对象 避免bug
        })
      }).catch(() => {});
    }
  }
}
</script>
<style lang="less" scoped>
  .btn{
    color: #0E2C55;
    &:hover{
      color:#2B8BFF;
    }
  }
</style>