import request from '@/utils/request'
// 获取店铺列表
export function getShopInfoList (id,t) {
  return request({
    url: `/sys/business/shopInfo/getShopInfoList?userId=${id}&shopType=${t}`,
    method: 'get',
  })
}
// 获取商户信息
export function getUserInfo (id) {
  return request({
    url: `/user/business/user/getUserInfo?userId=${id}`,
    method: 'get',
  })
}
// 冻结 || 解冻 || 删除 店铺
export function updateShopStatus(data) {
  return request({
    url: `/sys/business/shopInfo/updateShopStatus`,
    method: 'post',
    data: data
  })
}
// 修改店铺密码
export function updatePassword(data) {
  return request({
    url: `/user/business/user/updatePassword`,
    method: 'post',
    data: data
  })
}
// 查询店铺信息
export function getShopInfoById (id) {
  return request({
    url: `/sys/business/shopInfo/getShopInfoById?shopId=${id}`,
    method: 'get',
  })
}
// 修改用户信息
export function updateUserInfo(data) {
  return request({
    url: `/user/business/user/updateUserInfo`,
    method: 'post',
    data: data
  })
}
// 修改店铺信息
export function updateShopInfo(data) {
  return request({
    url: `/sys/business/shopInfo/updateShopInfo`,
    method: 'post',
    data: data
  })
}
